}

.nav-tabs {
    border-bottom: 1px solid #dee2e6;
}

.nav-tabs .nav-link {
    margin-bottom: -1px;
    color: #495057;
    border: 1px solid transparent;
    border-top-left-radius: 0.25rem;
    border-top-right-radius: 0.25rem;
}

.nav-tabs .nav-link.active {
    color: #495057;
    background-color: #fff;
    border-color: #dee2e6 #dee2e6 #fff;
}

.tab-content {
    padding: 10px;
    background-color: #fff;
    border: 1px solid #dee2e6;
    border-top: none;
}

.tab-pane {
    display: none;
}
.tab-pane.active {
    display: block;
}



/* 确保switch容器不会阻挡点击事件 */
#switch-area {
    pointer-events: auto;
    position: relative;
    z-index: 100;
}






/* 自定义表格样式 */
.table {
    width: 100%;
    table-layout: fixed;
    word-wrap: break-word;


    --bs-table-border-color: #afffff !important;       /* 边框颜色 */

    --bs-table-hover-color: #f56464 !important;        /* 悬停文字颜色 */
    --bs-table-hover-bg: #ebe3e3 !important;          /* 悬停背景颜色 */
    border: none !important;              /* 移除表格外边框 */
    border-collapse: collapse !important;  /* 确保边框合并 */
}


td, th {
    padding: 4px !important;
}

.table {
    margin-bottom: 0;
    min-width: auto !important;
}

/* 确保表格容器有正确的宽度和溢出处理 */
.table-responsive {
    width: 100%;
    overflow-x: hidden;
}



/* 专门设置表头样式 */
.table thead,
.table > thead{
    border: none !important;              
    background: linear-gradient(to right, #0D6E6E, #4a9d9c) !important;  
}

/* 确保表头单元格没有背景色 */
.table thead tr,
.table thead th {
    background: transparent !important;    /* 确保tr和th是透明的 */
    border: none !important;              
    color: #ffffff !important;            /* 表头文字颜色 */
}




/* 记忆概率指示器样式 */
.memory-indicator {
    display: inline-flex;
    align-items: center;
    padding: 4px 8px;
    border-radius: 12px;
    transition: all 0.3s ease;
}

.memory-indicator:hover {
    background-color: rgba(255, 255, 255, 0.1);
    transform: scale(1.05);
}

.memory-indicator i {
    font-size: 1.1em;
}

/* 颜色类 */
.text-success { color: #4caf50 !important; }
.text-warning { color: #ff9800 !important; }
.text-danger { color: #f44336 !important; }



/* 设置卡片样式调整 */
.option-card {
    background-color: #1d2e3d;
    border-radius: 10px;
    padding: 20px;
    text-align: center;
    transition: all 0.3s ease;
}

.option-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 16px rgba(74, 157, 156, 0.2);
}

.option-card i {
    font-size: 2em;
    color: #4a9d9c;
    margin-bottom: 15px;
}

.option-card h4 {
    color: #fff;
    margin-bottom: 15px;
}

.option-card p {
    color: #888;
    font-size: 0.9em;
}

/* 表单控件样式 */
.form-select {
    background-color: #0D1F2D;
    color: #fff;
    border: 1px solid #4a9d9c;
    margin-top: 10px;
}

.sync-tips {
    margin-top: 10px;
    font-size: 1.0em;
    color: #888;
    display: flex;
    flex-direction: column;
    gap: 5px;
}
.reminder-tips {
    margin-top: 10px;
    font-size: 1.0em;
    color: #888;
    display: flex;
    flex-direction: column;
    gap: 5px;
}
.save-section {
    grid-column: 1 / -1;
    text-align: center;
    margin-top: 20px;
}

/* 开关按钮样式 */
.form-check-input.custom-switch {
    background-color: #0D1F2D !important;
    border-color: #4a9d9c !important;
    box-shadow: 0 0 12px rgba(74, 157, 156, 0.9);
    transition: all 0.3s ease;
    cursor: pointer !important;
    pointer-events: auto !important;
    opacity: 1;
    z-index: 100;
    position: relative;
    outline: none !important;
    /* 自定义滑块圆圈颜色为亮蓝色 */
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) !important;
}

/* 选中状态下的样式 */
.form-check-input.custom-switch:checked {
    background-color: #0D6E6E !important;
    border-color: #0D6E6E !important;
    box-shadow: 0 0 12px rgba(74, 157, 156, 0.9);
    /* 选中状态下保持相同的蓝色圆圈 */
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) !important;
}

/* 悬停状态加强发光效果 */
.form-check-input.custom-switch:hover {
    box-shadow: 0 0 15px rgba(97, 218, 251, 0.9);
}

/* 焦点状态 */
.form-check-input.custom-switch:focus {
    background-color: inherit !important;  /* 继承当前状态的背景色 */
    border-color: #4a9d9c !important;
    box-shadow: 0 0 12px rgba(74, 157, 156, 0.9);
    outline: none !important;
}

/* 选中且焦点状态 */
.form-check-input.custom-switch:checked:focus {
    background-color: #4a9d9c !important;
}





/* SweetAlert2 自定义样式 */
.colored-toast.swal2-icon-success {
    background-color: #1d2e3d !important;
    border: 1px solid #4a9d9c !important;
}

.colored-toast .swal2-title {
    color: #ffffff !important;
    font-size: 1em !important;
}

.colored-toast .swal2-close {
    color: #4a9d9c !important;
}

.colored-toast .swal2-html-container {
    color: #888 !important;
    font-size: 0.9em !important;
}

/* 成功图标颜色 */
.colored-toast .swal2-success-line-tip,
.colored-toast .swal2-success-line-long {
    background-color: #4a9d9c !important;
}

.colored-toast .swal2-success-ring {
    border-color: #4a9d9c !important;
}

/* 更新概要样式 */
.update-badge {
    background-color: #FF3D3D;
    color: white;
    padding: 2px 5px;
    border-radius: 3px;
    font-size: 0.8em;
    margin-right: 5px;
    font-weight: bold;
}

.update-summary {
    background-color: rgba(74, 157, 156, 0.1);
    border-radius: 4px;
    padding: 3px 0 !important;
    margin-bottom: 8px !important;
}

.update-summary a {
    color: #4a9d9c;
    text-decoration: none;
    margin-left: 5px;
}

.update-summary a:hover {
    text-decoration: underline;
    color: #afffff;
}

/* 图标按钮样式 */
.btn-icon {
    background: none;
    border: none;
    color: #4a9d9c;
    font-size: 1em;
    width: 28px;
    height: 28px;
    border-radius: 6px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.3s ease;
    background-color: transparent;
}

.btn-icon:hover {
    background-color: rgba(74, 157, 156, 0.1);
    color: #61dafb;
    transform: translateY(-1px);
}

.btn-icon:active {
    transform: translateY(0);
}

.btn-icon-sm {
    width: 24px;
    height: 24px;
}

/* 优化参数进度条样式 */
.optimize-progress {
    height: 3px !important;
    background-color: rgba(74, 157, 156, 0.1) !important;
    border-radius: 4px !important;
    margin-top: 12px !important;
    overflow: hidden !important;
}

.optimize-progress .progress-bar {
    background: linear-gradient(90deg, #4a9d9c, #61dafb) !important;
    transition: width 0.3s ease !important;
}

.optimize-progress .progress-bar-animated {
    animation: progress-bar-stripes 1s linear infinite !important;
}

.optimize-progress .progress-bar-striped {
    background-image: linear-gradient(
        45deg,
        rgba(255, 255, 255, 0.15) 25%,
        transparent 25%,
        transparent 50%,
        rgba(255, 255, 255, 0.15) 50%,
        rgba(255, 255, 255, 0.15) 75%,
        transparent 75%,
        transparent
    ) !important;
    background-size: 1rem 1rem !important;
